<template>
    <div id="form">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-row>
        <el-col :span="12">
            <el-form-item label="" prop="email">
                <el-input v-model="ruleForm.email" placeholder=""></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="12">
            <el-form-item label="" prop="name" placeholder="">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
        </el-col>
        </el-row>
        <el-row>
            <el-form-item label="" prop="desc">
                <el-input type="textarea" v-model="ruleForm.desc" placeholder=""></el-input>
            </el-form-item>
        </el-row>
        <el-row>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-row>
    </el-form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          email:'',
          desc: ''
        },
        rules: {
          email: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: ['blur', 'change'] }
          ],
           name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
          ],
          desc: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid){
           try {
              this.$axios.post('/user/newMessages',this.ruleForm)
                .then(res=>{
                  this.$message({
                    message:"!",
                    type:'success'
                  })
                  this.resetForm(formName);
                  this.bus.$emit('changeReply');
                })
           } catch (err) {
             console.log(err);
           }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style scoped>
    .el-textarea >>> .el-textarea__inner{
       height:210px;
    }
</style>